@import url(https://fonts.googleapis.com/css?family=Roboto:400,400i,700);
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

@font-face {
  font-family: 'Gerstner Programm';
  src: url(/static/media/gerstnerprogramm-regular-webfont.519e8fa6.woff) format("woff");
  font-style: normal;
  font-weight: 400; }

body {
  background-color: black;
  font-family: 'Gerstner Programm', sans-serif;
  font-size: 14px;
  line-height: 16px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden; }

body, div, p, h1, h2, h3 {
  color: white; }

p, span, li {
  color: #e6e6e6;
  word-break: keep-all; }

body, div, p, ol, li {
  font-family: 'Gerstner Programm', sans-serif;
  font-size: 1em;
  line-height: 1.3em;
  font-style: normal;
  font-weight: 400;
  color: white;
  text-rendering: optimizeLegibility; }

/* Italicize titles of works */
cite {
  font-style: italic; }

input {
  -webkit-box-shadow: none;
  outline: -webkit-focus-ring-color auto 0px;
  border: none; }

a {
  text-decoration: none;
  color: white; }

a:hover, a:focus {
  opacity: .5;
  -webkit-transition: all 0.1s ease-out;
  transition: all 0.1s ease-out; }

.coverage-graph {
  position: fixed;
  right: 150px;
  padding-top: 10px;
  padding-left: 20px;
  top: 10px;
  z-index: 4;
  background-color: rgba(0, 0, 0, 0.7); }

b {
  font-weight: 700;
  color: white; }

.fl {
  float: left; }

.marker {
  width: 10px;
  height: 10px;
  background-color: white;
  border-radius: 10px; }

.bold {
  font-size: 1.0em; }

.fr {
  float: right; }

.l-apple-box {
  height: 20px; }

.l-apple-box--half {
  height: 10px; }

.l-apple-box--double {
  height: 40px; }

br.clearing {
  clear: both !important; }

sup {
  font-size: 0.6em;
  position: relative;
  top: -0.6em; }

.linear-activity {
  overflow: hidden;
  width: 100%;
  height: 3px;
  background-color: black; }

.indeterminate {
  position: relative;
  width: 100%;
  height: 100%; }

.indeterminate:before {
  content: '';
  position: absolute;
  height: 100%;
  background-color: #cccccc;
  -webkit-animation: indeterminate_first 1.5s infinite ease-out;
          animation: indeterminate_first 1.5s infinite ease-out; }

@-webkit-keyframes indeterminate_first {
  0% {
    left: -100%;
    width: 100%; }
  100% {
    left: 100%;
    width: 10%; } }

@keyframes indeterminate_first {
  0% {
    left: -100%;
    width: 100%; }
  100% {
    left: 100%;
    width: 10%; } }

.summary-page {
  padding: 30px; }

.summary-title {
  font-weight: bold;
  font-size: 1.2em;
  margin-bottom: 20px; }

.summary-container {
  margin-bottom: 50px; }

.Typist .Cursor {
  display: inline-block; }

.Typist .Cursor--blinking {
  opacity: 1;
  -webkit-animation: blink 1s linear infinite;
          animation: blink 1s linear infinite; }

@-webkit-keyframes blink {
  0% {
    opacity: 1; }
  50% {
    opacity: 0; }
  100% {
    opacity: 1; } }

@keyframes blink {
  0% {
    opacity: 1; }
  50% {
    opacity: 0; }
  100% {
    opacity: 1; } }

.react-pdf__Page__textContent {
  display: none; }

